










































































import Vue from "vue";
import Component from "vue-class-component";
import FeedItemComponent from "@/components/feed/FeedItem.vue";
import FeedComposeComponent from "@/components/feed/FeedCompose.vue";
import { absolutePath, get } from "@/util/BackendHelper";

@Component({
  components: {
    FeedItem: FeedItemComponent,
    FeedCompose: FeedComposeComponent,
  },
})
export default class PoolPage extends Vue {
  feedItems: any[] = [];

  samplePool :any = {
    name: "",
    summary: "",
    members: [],
    admin: "",
    tags: [],
    imageUrl: "",
    posts: [],
    createdAt: "",
  };

  averageColor = "white";

  infoState = "정보";

  getAverageRGB(imgEl :any) :any {
    const blockSize = 2;
    const defaultRGB = {
      r: 0,
      g: 0,
      b: 0,
    };
    const canvas = document.createElement("canvas");
    const context = canvas.getContext && canvas.getContext("2d");
    let data;
    let i = 0;
    const rgb = {
      r: 0,
      g: 0,
      b: 0,
    };
    let count = 0;

    if (!context) {
      console.log("context error");
      return defaultRGB;
    }

    canvas.height = imgEl.naturalHeight || imgEl.offsetHeight || imgEl.height;
    const { height } = canvas;
    console.log(height);

    canvas.width = imgEl.naturalWidth || imgEl.offsetWidth || imgEl.width;
    const { width } = canvas;

    context.drawImage(imgEl, 0, 0);

    try {
      data = context.getImageData(0, 0, width, height);
      console.log(data);
    } catch (e) {
      console.log(e);
      return defaultRGB;
    }

    const { length } = data.data;

    while (data.data[i]) {
      count += 1;
      rgb.r += data.data[i];
      rgb.g += data.data[i + 1];
      rgb.b += data.data[i + 2];
      i += blockSize * 4;
    }

    rgb.r = Math.floor(rgb.r / count);
    rgb.g = Math.floor(rgb.g / count);
    rgb.b = Math.floor(rgb.b / count);

    return rgb;
  }

  addImageProcess(src :string) {
    return new Promise((resolve, reject) => {
      const img = new Image();
      img.onload = () => resolve(img);
      img.onerror = reject;
      img.src = src;
      img.crossOrigin = "Anonymous";
    });
  }

  async setAverageColor(src :string) :Promise<void> {
    const img = await this.addImageProcess(src);
    const averageRGB = this.getAverageRGB(img);
    this.averageColor = `rgba(${averageRGB.r}, ${averageRGB.g}, ${averageRGB.b}, 1)`;
  }

  async created(): Promise<void> {
    const response :any = await get(`pools/${this.$route.params.id}`);

    if (response.status >= 400) {
      // error
    } else {
      const { data } = response;
      let { createdAt } = data;
      createdAt = new Date(createdAt);
      createdAt = `${createdAt.getFullYear()}-${createdAt.getMonth()}-${createdAt.getDate()}`;
      this.samplePool = {
        ...data,
        createdAt,
        imageUrl: `${absolutePath(data.image.url)}`,
        tags: data.tags.map((el :any) => el.content),
      };
      this.setAverageColor(this.samplePool.imageUrl);
    }

    /* get feeds */
    const response2 :any = await get("/posts");
    if (response2.status >= 400) {
      // error
    } else {
      let { data } = response2;
      data = data.filter((el: any) => {
        if (el.postType === "pool") {
          return this.samplePool.name === el.pool.name;
        }
        return false;
      });
      console.log(data);
      this.feedItems = await Promise.all(data.map(async (el :any) => {
        const response3 = await get(`/posts/preview/${el.id}`);
        const newPostData: any = response3.data;
        newPostData.createdAt = new Date(el.createdAt);
        return {
          postInfo: newPostData,
          likedByAccount: false,
        };
      }));
    }
  }

  onClick1(val :string) {
    this.infoState = val;
  }
}
