interface ISubject {
  name: string,
  tags: Array<string>,
}

interface IFilter {
  name: string,
  subjects: Array<ISubject>
}

interface ITag {
  subject: string,
  tag: string,
}

interface IDiscoverRecruition {
  id: string,
  author: {
    id: string,
    username: string,
    profileImageUrl: string,
  }
  title: string,
  employmentType: string,
  pay: string,
  due: string,
  relatedBranches: Array<string>
}

interface IDiscoverPool {
  id: string,
  name: string,
  summary: string,
  tags: Array<string>,
  memberCount: number,
  imageUrl: string,
}

interface IDiscoverUser {
  id: string,
  username: string,
  birthday: string,
  telno: string,
  location: string,
  email: string,
  summary: string,
  department: string,
  tags: Array<string>,
  militaryBranch: string,
  role: string,
  profileImageUrl: string,
  postCount: number,
  followersCount: number,
}

